<template>
  <div class="articles" v-loading="loading">
    <div class="common-top">
      <div class="common-btnbox">
        <el-button type="primary" size="small" @click="addInfo"></el-button>
      </div>
    </div>
    <el-table :data="listData" style="width: 100%">
      <el-table-column prop="title" label=""></el-table-column>
      <el-table-column prop="type" label=""></el-table-column>
      <el-table-column prop="time" label="">
        <template slot-scope="scope">
          <span>{{getTime(scope.row.time)}}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="140">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="updateInfo(scope.row)" icon="el-icon-edit"></el-button>
          <el-button
            @click="delInfo(scope.row)"
            type="text"
            size="small"
            class="danger"
            icon="el-icon-error"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="common-page">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :page-size="limit"
        :total="totalCount"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>

    <el-dialog :title="title" :visible.sync="formVisible" width="1200px">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.abstract" autocomplete="off" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-upload
            class="avatar-uploader"
            action="http://localhost:3000/imgUp"
            ref="uploadImg"
            :show-file-list="false"
            :file-list="fileList"
            :on-success="handleAvatarSuccess"
            :on-change="changeImage"
            :on-remove="handleRemove"
            :auto-upload="false"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <quill-editor v-model="form.content" ref="myQuillEditor" :options="editorOption"></quill-editor>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="formVisible = false"> </el-button>
        <el-button type="primary" @click="editImagesSure"> </el-button>
        <el-button type="text" @click="preview()"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delVisible" width="400px">
      <div class="common-dialog-content"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delVisible = false"> </el-button>
        <el-button type="primary" @click="sureDel()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="1200px">
      <div class="common-dialog-preview">
        <div class="quill-editor">
          <div class="ql-container ql-snow">
            <div class="ql-editor" style="height:auto;" v-html="form.content"></div>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="previewVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getListData, createData, deleteData, updateData } from "@/api/article";
import { filterFormatTime, filterObj } from "@/utils/filter";
import { quillEditor } from "vue-quill-editor"; //
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

export default {
  name: "articles",
  components: {
    quillEditor
  },
  data() {
    return {
      loading: true,

      tableParams: {
        page: 0,
        limit: 0
      },
      page: 1,
      limit: 2,
      totalCount: 0,

      listData: [],
      formVisible: false,
      title: "",
      formLabelWidth: "80px",
      form: {
        title: "",
        type: "1",
        content: "",
        abstract: "",
        imgId: ""
      },
      editorOption: {},
      options: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        }
      ],
      type: "",
      optId: "",
      delVisible: false,
      type: "",
      optId: "",
      previewVisible: false,

      // 
      imageUrl: "",
      fileList:[]
    };
  },
  methods: {
    handleAvatarSuccess(res, file) {
      console.log(res,file);
      this.form.imgId = res.id;

      this.editSure();
    },
    changeImage(file, fileList) {
      this.fileList = [file];
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    handleRemove() {
      this.fileList = [];
    },
    editImagesSure() {
      if(this.fileList.length != 0) {
        this.$refs.uploadImg.submit();
      }else {
        this.editSure();
      }
    },


    preview() {
      this.previewVisible = true;
    },
    updateInfo(row) {
      this.type = "update";
      this.title = "";
      this.optId = row._id;

      this.initData();

      this.form = {
        title: row.title,
        type: row.type,
        content: row.content,
        abstract: row.abstract,
        imgId: row.imgId
      };

      this.imageUrl = row.imgId ? "http://localhost:3000/imgUp/file?id=" + row.imgId : "";

      this.formVisible = true;
    },
    initData() {
      this.form = {
        title: "",
        type: "1",
        content: "",
        abstract: "",
        imgId: "",
      };

      this.imageUrl = "";
    },
    delInfo(row) {
      this.optId = row._id;

      this.delVisible = true;
    },
    sureDel() {
      this.loading = true;

      deleteData({ _id: this.optId })
        .then(res => {
          this.loading = false;
          this.delVisible = false;

          this.$message({
            type: "success",
            message: ""
          });

          this.tableParams.page = 1;
          this.page = 1;
          this.getList();
        })
        .catch(err => {
          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    handleCurrentChange(val) {
      this.tableParams.page = val;

      this.getList();
    },
    getTime(time) {
      return filterFormatTime(time);
    },
    onEditorReady(editor) {
      // 
    },
    addInfo() {
      this.type = "add";
      this.title = "";

      this.initData();
      this.formVisible = true;
    },
    onEditorBlur() {}, // 
    onEditorFocus() {}, // 
    onEditorChange() {}, // 
    getList() {
      getListData(filterObj(this.tableParams)).then(res => {
        this.loading = false;

        this.listData = res.data.list;
        this.totalCount = res.data.count;
      });
    },
    editSure() {
      let data = {
        title: this.form.title,
        type: this.form.type,
        content: this.form.content,
        abstract: this.form.abstract,
        imgId: this.form.imgId
      };

      this.loading = true;
      if (this.type == "add") {
        createData(filterObj(data))
          .then(res => {
            this.formVisible = false;
            this.loading = false;

            this.$message({
              type: "success",
              message: ""
            });

            this.getList();
          })
          .catch(err => {
            this.formVisible = false;
            this.loading = false;

            this.$message({
              type: "error",
              message: ""
            });
          });
      } else if (this.type == "update") {
        data.id = this.optId;

        updateData(filterObj(data))
          .then(res => {
            this.formVisible = false;
            this.loading = false;

            this.$message({
              type: "success",
              message: ""
            });

            this.getList();
          })
          .catch(err => {
            this.formVisible = false;
            this.loading = false;

            this.$message({
              type: "error",
              message: ""
            });
          });
      }
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  mounted() {
    this.tableParams.page = this.page;
    this.tableParams.limit = this.limit;
    this.getList();
  }
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
