import request from'../utils/request'

export function getListData(params) {
    return request({
        method: "GET",
        url: "/article/list",
        params
    })
}

export function createData(data) {
    return request({
        method: "POST",
        url: "/article/create",
        headers: { "Content-Type": "application/json" },
        data
    })
}

export function deleteData(data) {
    return request({
        method: "POST",
        url: "/article/delete",
        headers: { "Content-Type": "application/json" },
        data
    })
}

export function updateData(data) {
    return request({
        method: "POST",
        url: "/article/update",
        headers: { "Content-Type": "application/json" },
        data
    })
}